.loader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000000;
  border-radius: inherit;

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    background-color: var(--bg, #fff);
    opacity: 0.95;
    border-radius: inherit;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    height: 90%;
    max-width: 200px;
    max-height: 200px;
    background-repeat: no-repeat;
    background-position: center center;
    background-image: url(./loader-eclipse-1s-200px.svg);
    background-size: contain;
  }

  &-opaque {
    background-color: rgba(#fff, 0.9);
  }

  &.loading {
    opacity: 1;
  }

  &--fixed {
    position: fixed !important;
  }
}

body > .loader {
  position: fixed !important;
}
