.pagination {
  --accent-color: var(--accent, #2099e5);

  &__body {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 8px;
  }

  &__btn {
    width: 28px;
    height: 28px;
    display: inline-block;
    line-height: 1;
    background-color: transparent;
    border: 1px solid transparent;
    transition: opacity .3s ease-in-out, border-color .15s ease-in-out;
    border-radius: 4px;
    position: relative;
    cursor: pointer;
    outline-color: var(--accent-color);

    &:hover,
    &.focus-visible {
      border-color: var(--accent-color);
    }

    &:active {
      opacity: 0.7;
    }

    &:disabled {
      opacity: 0.5;
      cursor: auto;
      filter: grayscale(0.5);
    }

    svg {
      position: absolute;
      inset: 0;
      width: 18px;
      height: 18px;
      margin: auto;
      fill: var(--accent-color);
    }

    &--first {
      svg {
        transform: rotate(180deg);
      }
    }

    &--prev {
      svg {
        transform: rotate(180deg);
      }
    }
  }

  &__pages {
    padding-top: 2px;
    opacity: 0.8;
  }

  &__spacer {
    display: inline-block;
    padding: 0 6px;
  }
}