.nav-cards {
  margin-top: 64px;
  margin-inline: auto;
  max-width: 600px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
  $that: &;

  &__card {
    aspect-ratio: 1;
    border: 1px solid var(--accent);
    border-radius: 12px;
    position: relative;
    isolation: isolate;
    transition: background-color 0.15s ease-in-out;

    svg {
      position: absolute;
      top: 40%;
      left: 50%;
      transform: translate(-50%, -50%);
      fill: var(--accent);
      transition: fill 0.15s ease-in-out;
    }

    &:hover,
    &.focus-visible,
    &:active {
      background-color: var(--accent);

      svg {
        fill: #fff;
      }

      #{$that}__title {
        color: #fff;
      }
    }

    &.focus-visible {
      outline: 2px solid var(--highlight);
      outline-offset: 2px;
    }
  }

  &__title {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 16px;
    font-size: 2rem;
    margin: 0;
    text-align: center;
    color: var(--accent);
    font-weight: 400;
    transition: color 0.15s ease-in-out;
  }
}
