html.no-overflow, 
body.no-overflow {
  overflow: hidden;
}

.modal {
  padding: 24px;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  background-color: var(--modal-bg);
  text-align: left;
  border-radius: 12px;
  box-shadow: 0 0 6px rgba(183, 183, 183, 0.32);
  --modal-bg: var(--bg, #fff);
  --modal-accent: var(--accent, #2d83da);
  $that: &;

  &-overlay {
    padding: 32px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(0 0 0 / 80%);
    z-index: 100;
    text-align: center;
    overflow: auto;

    &::before {
      content: '';
      display: inline-block;
      height: 100%;
      vertical-align: middle;
    }
  }

  &--wide {
    #{$that}__container {
      max-width: 600px;
    }
  }

  &--info {
    #{$that}__container {
      max-width: 280px;
    }
  }

  &__container {
    max-width: 400px;
    display: flex;
    flex-direction: column;
    gap: 8px;

    & > *:empty {
      display: none;
    }
  }

  &__title,
  &__heading {
    text-align: center;
    font-weight: 400;
    font-size: 2.4rem;
  }

  &__heading {
    margin-bottom: 24px;
  }

  &__icon {
    width: 64px;
    height: 64px;
    margin: 0 auto;
    position: relative;
    margin-bottom: 8px;

    svg {
      position: absolute;
      inset: 0;
      width: 100%;
      height: 100%;
      fill: var(--icon-fill);
    }

    &--success {
      --icon-fill: #3d9d57;
    }

    &--warning {
      --icon-fill: #eb6657;
    }

    &--error {
      --icon-fill: #e65361;
    }
  }

  &__detail-wrap {
    display: flex;
    flex-direction: column;
    gap: 4px;

    & > *:empty {
      display: none;
    }
  }

  &__detail-text {
    font-size: 1.6rem;
    text-align: center;
  }

  &__btn-wrap {
    margin-top: 8px;
    margin-bottom: -8px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 8px;
    flex-wrap: wrap;
  }

  &__btn {
    cursor: pointer;
    background-color: transparent;
    border: 1px solid transparent;
    line-height: 1;
    position: relative;
    border-radius: 4px;
    outline-color: var(--btn-outline, var(--modal-accent));
    transition: opacity .15s ease-in-out, filter .15s ease-in-out;
    font-size: 1.6rem;
    font-weight: 400;

    &:active {
      opacity: 0.7;
    }

    &--close-top {
      width: 28px;
      height: 28px;
      position: absolute;
      top: 8px;
      right: 8px;
      border-radius: 8px;

      &::after {
        content: '';
        position: absolute;
        inset: 0;
        border-radius: inherit;
        border: 1px solid var(--modal-accent);
        opacity: 0;
        transition: opacity .15s ease-in-out;
      }

      svg {
        position: absolute;
        inset: 0;
        width: 16px;
        height: 16px;
        margin: auto;
        transition: fill .15s ease-in-out;
        opacity: 0.5;
      }

      &:hover,
      &.focus-visible {
        &::after {
          opacity: 0.1;
        }

        svg {
          fill: var(--modal-accent);
        }
      }
    }

    &--main {
      padding: 6px 24px;
      border-radius: 100px;
      outline-offset: 3px;
    }

    &--ok {
      background-color: var(--modal-accent);
      color: var(--text-white, #fff);

      &:hover,
      &.focus-visible {
        filter: saturate(1.5);
      }
    }
  }
}
