.header {
  box-shadow: 0 0 12px #16161834;
  color: var(--accent);
  background-color: var(--accent);
  color: var(--text-white);
  
  &__container {
    padding: 16px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  &__burger {
    width: 36px;
    height: 20px;
    border: none;
    background-color: transparent;

    span {
      position: absolute;
      transform-origin: left;
      width: 100%;
      height: 2px;
      background-color: currentColor;
      left: 0;
      transition: transform .2s ease-in-out;

      &:nth-child(1) {
        top: 0;
        --scale-down: 0;
      }

      &:nth-child(2) {
        top: calc(50% - 1px);
        --scale-down: 0.2;
      }

      &:nth-child(3) {
        bottom: 0;
        --scale-down: 0.3;
      }
    }

    &:hover,
    &.focus-visible {
      span {
        transform: scale(calc(1 - var(--scale-down)));
      }
    }
  }

  &__menu {
    padding: 64px;
    background-color: var(--bg);
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    box-shadow: 0 0 12px #16161834;
    transition: transform .15s ease-in-out;
    transform: translateX(-110%);
    color: var(--text);
    z-index: 1000;

    &-enter {
      display: block;
    }

    &-enter-active,
    &-enter-done,
    &-exit {
      display: block;
      transform: translateX(0);
    }

    &-exit-active {
      transform: translateX(-110%);
    }
  }

  &__close-menu {
    position: absolute;
    top: 8px;
    right: 6px;
    width: 24px;
    height: 24px;
    border: 1px solid transparent;
    background-color: transparent;
    transition: opacity .3s ease-in-out, border-color .15s ease-in-out;
    border-radius: 4px;

    span {
      position: absolute;
      inset: 0;
      width: 80%;
      height: 1px;
      margin: auto;
      background-color: currentColor;

      &:first-child {
        transform: rotate(45deg);
      }

      &:last-child {
        transform: rotate(-45deg);
      }
    }

    &:hover,
    &.focus-visible {
      opacity: 0.8;
      border-color: var(--bd-light);
    }

  }

  &__nav {
    &-list {
      flex-direction: column;
      align-items: flex-start;
      gap: 24px;
    }
  }

  &__user {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 12px;

    &-auth {
      padding: 4px 12px;
      background-color: transparent;
      border-radius: 100px;
        background-color: var(--accent);
      border: 1px solid currentColor;
      transition: opacity .15s ease-in-out;

      &:hover,
      &.focus-visible {
        opacity: 0.8;
      }
    }
  }
}