@import "mixins";
@import "normalize";
@import "fonts";
@import "visuallyhidden";

html {
  --white: #fff;
  --light: #f2f2f7;

  --grey-lighter-3: #e3e4e8;
  --grey-lighter-2: #bbbdc3;
  --grey-lighter: #a6a6a6;
  --grey-light: #666;
  --grey: #555;
  --grey-dark: #444;
  --grey-darker: #333;
  --grey-darker-2: #1c1c1c;
  --grey-darker-3: #202124;

  --black: #000;

  --blue-dark: #1856b3;
  --blue: #3c7ddc;
  --blue-intense: #1c7ff2;
  --blue-green: #005066;

  --mint: #006064;

  --darkorange: #bb5730;
  --danger: #d51c15;

  --text-white: var(--white);
  --text-grey: var(--grey-light);
  --text: var(--grey-darker-2);

  --bg: var(--light);

  --bd-lighter: var(--grey-lighter-3);
  --bd-light: var(--grey-lighter-2);

  --accent: var(--blue-green);
  --highlight: var(--blue);

  font-size: 62.5%;

  &[data-theme="dark"] {
    --bg: var(--grey-darker-3);
    --text: var(--white);
  }
}

*,
::before,
::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

::-webkit-scrollbar {
  width: 8px;
  height: 8px;

  &-track {
    margin: 4px;
    border-radius: 10px;
    background-color: #9993;
    border: 1px solid transparent;

    &:hover {
      border-color: #aaaa;
    }
  }

  &-thumb {
    background-color: #aaaa;
    border-radius: 10px;
    min-height: 30px;

    &:hover {
      background-color: #aaa;
    }
  }

  &-corner {
    display: none;
  }
}

@supports (scrollbar-width: thin) {
  * {
    scrollbar-width: thin;
    scrollbar-color: #aaa #eee;
  }
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.hidden,
[hidden] {
  display: none !important;
}

:focus:not(:focus-visible) {
  outline: none !important;
}

:focus:not(.focus-visible) {
  outline: none !important;
}

a,
button,
input {
  font: inherit;
}

a,
button {
  color: inherit;
}

body {
  background-color: var(--bg);
  color: var(--text);
  font-family: Nunito, Roboto, "Segoe UI", sans-serif, Helvetica, Arial;
  font-size: 1.8rem;
}

.container {
  max-width: 1040px;
  padding: 0 20px;
  margin: 0 auto;

  &--align-center {
    text-align: center;
  }
}

.link {
  position: relative;
  display: inline-block;
  transition: opacity 0.3s ease-in-out;
  outline-offset: 2px;
  line-height: 1;

  &.focus-visible {
    outline: 2px solid var(--highlight);
    outline-offset: 2px;
  }

  &:active {
    opacity: 0.7;
  }
}

.btn {
  position: relative;
  line-height: 1;
  cursor: pointer;
  flex-shrink: 0;
  outline-offset: 2px;
  transition: opacity 0.3s ease-in-out, color 0.15s ease-in-out, border-color 0.15s ease-in-out,
    background-color 0.15s ease-in-out, filter 0.15s ease-in-out;

  &:active {
    opacity: 0.7;
  }

  &.focus-visible {
    outline: 2px solid var(--highlight);
    outline-offset: 2px;
  }
}

.theme-switch {
  display: block;
  $that: &;

  &__content {
    position: relative;
    display: block;
    height: 24px;
    width: 48px;
    border-radius: 100px;
    box-shadow: 2px 2px 4px #dadade, -2px -2px 4px #ffffff;
    cursor: pointer;

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 4px;
      transform: translateY(-50%);
      display: block;
      width: 16px;
      height: 16px;
      border-radius: 50%;
      background-color: #ffd057;
      box-shadow: 0 0 6px #ffd057, 0 0 4px #ffd057, 0 0 2px #ffd057;
    }
  }

  input:checked ~ #{$that}__content {
    box-shadow: 2px 2px 4px #161618, -2px -2px 4px #2a2c30;

    &::after {
      left: unset;
      right: 4px;
      background-color: #331f8d;
      box-shadow: 0 0 6px #331f8d, 0 0 4px #331f8d, 0 0 2px #331f8d;
    }
  }
}

.heading-1 {
  flex: 2.4rem;
  margin: 32px auto 48px;
  text-align: center;
  font-weight: 400;
}

.text-status {
  padding: 4px 6px;
  display: inline-block;
  line-height: 1;
  font-size: 1.4rem;
  color: var(--text-white);
  border-radius: 2px;
  background-color: var(--accent);

  &--draft {
    background-color: #b14359;
  }

  &--signed {
    background-color: #2367ab;
  }

  &--active {
    background-color: #29854c;
  }

  &--rejected {
    background-color: #6a6a6a;
  }
}

.nav {
  &__list {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 12px;
  }

  &__link {
    padding: 4px 16px;
    border-radius: 50px;
    border: 1px solid transparent;

    &:hover {
      border-color: var(--bd-lighter);
      opacity: 0.8;
    }
  }
}

.game {
  margin: 64px auto 32px;
  padding: 32px;
  width: max-content;
  border-radius: 12px;
  border: 1px solid var(--bd-light);
  display: flex;
  gap: 32px;

  &__board {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 8px;
  }

  &__card {
    min-width: 120px;
    aspect-ratio: 1;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: var(--grey-lighter-2);
    color: var(--text-white);
    font-size: 2.4rem;
    border-radius: 8px;
    line-height: 1;
    cursor: pointer;
  }

  &__panel {
    padding: 16px 0;
    min-width: 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    gap: 16px;
  }

  &__status {
    font-weight: 400;
    font-size: inherit;
  }

  &__btn {
    cursor: pointer;
    display: inline-block;
    line-height: 1;
    border: none;
    position: relative;
    transition: opacity 0.3s ease-in-out, color 0.15s ease-in-out, background-color 0.15s ease-in-out,
      border-color 0.15s ease-in-out;

    &:active {
      opacity: 0.7;
    }

    &-reset {
      padding: 6px 24px;
      --bd: #aaa;
      color: var(--text-grey);
      border: 1px solid var(--bd);
      background-color: transparent;
      border-radius: 50px;
      outline: none;

      &::before {
        content: "";
        position: absolute;
        inset: 0;
        border-radius: inherit;
        box-shadow: inset 0 0 6px var(--bd);
        opacity: 0;
        transition: opacity 0.15s ease-in-out;
      }

      &:hover {
        &::before {
          opacity: 0.5;
        }
      }

      &.focus-visible {
        &::before {
          opacity: 1;
        }
      }
    }
  }

  &__hist {
    list-style-type: none;
    padding: 16px;
    border-radius: 8px;
    border: 1px solid var(--bd-light);
    max-height: 200px;
    overflow: auto;

    &:empty {
      display: none;
    }

    &-item {
      display: block;

      &:not(:last-of-type) {
        margin-bottom: 4px;
      }
    }

    &-btn {
      padding: 4px 8px;
      width: 100%;
      border-radius: 50px;
      background-color: transparent;
      border: 1px solid transparent;
      font-size: 16px;
      font-weight: 300;

      &:hover,
      &.focus-visible {
        border-color: var(--bd-light);
      }
    }
  }
}

.form {
  position: relative;
  --form-bg: var(--bg);
  background-color: var(--form-bg);
  $that: &;

  &--single-row {
    #{$that}__row {
      margin-bottom: 0;
    }
  }

  &__columns {
    margin-bottom: 24px;
    display: flex;
    justify-content: center;
    gap: 16px;
    flex-wrap: wrap;
  }

  &__col {
    #{$that}__row:last-child {
      margin-bottom: 0;
    }
  }

  &__row {
    margin-bottom: 24px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 16px;
  }

  &__fields-wrap {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 16px;

    &--maw {
      &-700 {
        max-width: 700px;
      }
    }
  }

  &__field-wrap {
    position: relative;

    &--grow {
      flex-grow: 1;
    }

    &--select {
      flex-shrink: 0;
      flex-basis: 154px;
    }

    &--input-narrow {
      flex-shrink: 0;
      flex-basis: 120px;
    }
  }

  &__label {
    display: flex;
    align-items: center;
    position: relative;
    --text-offset: 8px;
    --bd-color: var(--bd-light);

    &.error {
      --bd-color: var(--danger);
      color: var(--danger);
    }

    &-text {
      padding: 0 4px;
      top: 0;
      left: var(--text-offset);
      max-width: calc(100% - 2 * var(--text-offset));
      transform: translateY(-50%);
      position: absolute;
      display: block;
      line-height: 1;
      background-color: var(--form-bg);
      font-size: 1.4rem;
    }
  }

  &__input {
    padding: 8px 16px;
    border: 1px solid var(--bd-color, #333);
    border-radius: 4px;
    background-color: var(--form-bg);
    outline: none;
    width: 100%;
    font-size: 1.6rem;
    color: inherit;

    &:focus {
      border-color: var(--accent);
    }

    &:disabled {
      color: var(--grey-lighter);
    }

    &--narrow {
      max-width: 100px;
    }

    &--narrower {
      max-width: 64px;
    }

    &--border-bottom {
      text-align: center;
      border-radius: 0;
      border-bottom: 1px solid var(--bd-light);
    }
  }

  &__btn-wrap {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 8px;
  }

  &__btn {
    &--submit {
      padding: 8px 24px;
      border-radius: 4px;
      outline-color: var(--accent);
      background-color: var(--accent);
      color: var(--text-white);
      font-size: 1.6rem;
      border: 1px solid var(--accent);
      min-width: 100px;
      // border-radius: 100px;

      &:hover,
      &.focus-visible {
        filter: brightness(1.2);
      }
    }

    &--cancel {
      padding: 8px 24px;
      border-radius: 4px;
      outline-color: var(--accent);
      background-color: transparent;
      color: var(--accent);
      font-size: 1.6rem;
      border: 1px solid var(--accent);
      min-width: 100px;
      // border-radius: 100px;

      &:hover,
      &.focus-visible {
        color: var(--text-white);
        background-color: var(--accent);
      }
    }

    &-reset {
      padding: 4px 6px 4px 24px;
      position: absolute;
      top: -26px;
      right: 8px;
      background-color: transparent;
      border: 1px solid transparent;
      border-radius: 50px;
      font-size: 1.6rem;
      opacity: 0.7;

      svg {
        width: 18px;
        height: 18px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 2px;
      }

      &:hover,
      &.focus-visible {
        opacity: 1;
        border-color: var(--bd-light);
      }
    }
  }

  &__range-dash {
    opacity: 0.5;
    margin: 0 8px;
  }

  &__unit {
    opacity: 0.5;
    margin-left: 8px;
  }

  &__alert {
    margin-top: 4px;
    padding-left: 12px;
    display: block;
    font-size: 1.4rem;
    color: var(--danger);
    text-align: left;

    &:empty {
      display: none;
    }

    &-large {
      font-size: 1.6rem;
      color: var(--danger);
    }
  }

  @include tablet-portrait {
    &__row {
      &--md-col {
        flex-direction: column;
      }
    }

    &__fields-wrap {
      &--md-wrap {
        flex-wrap: wrap;
      }
    }

    &__field-wrap {
      flex-grow: 1;
    }
  }
}

.table-block {
  padding: 16px 0;
  display: inline-block;
  max-width: 100%;
  border-radius: 24px;
  box-shadow: 0 0 4px var(--bd-light);
  --padding-inline: 16px;
  text-align: left;
  position: relative;

  &--min-height {
    &-400 {
      min-height: 400px;
    }
  }

  &__form {
    margin: 16px auto;
    padding: 8px;
  }

  &__scroll-wrap {
    padding: 0 var(--padding-inline);
    overflow: auto;

    &--mah-500 {
      max-height: 530px;
    }
  }

  &__table {
    width: 100%;
    position: relative;
    border-collapse: collapse;
  }

  &__btn {
    &--delete {
      margin: 0 auto;
      display: block;
      width: 24px;
      height: 24px;
      border-radius: 4px;
      border: none;
      outline-color: var(--accent);
      background-color: var(--accent);

      span {
        position: absolute;
        inset: 0;
        margin: auto;
        width: 75%;
        height: 1px;
        background-color: var(--white);

        &:first-child {
          transform: rotate(45deg);
        }

        &:last-child {
          transform: rotate(-45deg);
        }
      }
    }

    &--details {
      width: 28px;
      height: 28px;
      border-radius: 4px;
      border: none;
      outline-color: var(--accent);

      &::before {
        content: "";
        position: absolute;
        inset: 0;
        border-radius: inherit;
        border: 1px solid var(--accent);
        opacity: 0;
        transition: opacity 0.15s ease-in-out;
      }

      svg {
        position: absolute;
        inset: 0;
        width: 20px;
        height: 20px;
        margin: auto;
        fill: var(--accent);
      }

      &:hover,
      &.focus-visible {
        &::before {
          opacity: 0.4;
        }
      }
    }
  }

  &__loading {
    min-width: 590px;
    min-height: 300px;
  }

  &__empty {
    text-align: center;
    margin: 64px auto 72px;
    color: var(--accent);
    font-size: 3rem;
  }

  &__pagination {
    padding: 8px 0 0;
  }

  &--center-first {
    th:first-child,
    td:first-child {
      text-align: center;
    }
  }

  .sort-btn {
    padding: 12px;
    position: relative;
    display: inline-block;
    cursor: pointer;
    background-color: transparent;
    border: none;
    font: inherit;
    color: inherit;

    &.asc::after {
      content: "";
      display: block;
      position: absolute;
      top: 50%;
      right: -8px;
      transform: translateY(-50%);
      width: 1px;
      height: 1px;
      border-color: currentColor transparent;
      border-style: solid;
      border-width: 0 4px 6px;
    }

    &.des::after {
      content: "";
      display: block;
      position: absolute;
      top: 50%;
      right: -8px;
      transform: translateY(-50%) rotate(180deg);
      width: 1px;
      height: 1px;
      border-color: currentColor transparent;
      border-style: solid;
      border-width: 0 4px 6px;
    }
  }

  th {
    padding: 16px;
    text-align: left;

    &.sortable-cell {
      padding: 4px;
    }

    &.rc-table-cell-scrollbar {
      padding: 0;
    }
  }

  td {
    padding: 16px;
  }

  thead tr {
    position: relative;
    isolation: isolate;

    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: calc(-1 * var(--padding-inline));
      right: calc(-1 * var(--padding-inline));
      bottom: 0;
      background-color: var(--grey-lighter-3);
      z-index: -1;
      opacity: 0.6;
    }
  }

  tbody tr:not(:last-child) {
    border-bottom: 1px solid var(--bd-light);
  }

  .text-centered {
    text-align: center;
  }

  .no-padding {
    padding: 0;
  }

  .unit {
    opacity: 0.8;
    margin-left: 6px;
    font-weight: 300;
  }
}

.sidebar {
  width: 48px;
  padding: 16px var(--padding-inline);
  position: fixed;
  top: 100px;
  left: var(--offset-left);
  background-color: var(--bg);
  box-shadow: 8px 8px 12px #e1e1e6, -8px -8px 12px #ffffff;
  transition: width 0.3s ease-in-out;
  isolation: isolate;
  --offset-left: 8px;
  --padding-inline: 8px;
  --maw: calc(100% - 2 * var(--offset-left));
  --width-expanded: 800px;
  $that: &;

  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    left: -20px;
    top: 0;
    bottom: 0;
    right: 0;
  }

  &.active {
    width: min(var(--maw), calc(var(--width-expanded) + var(--padding-inline)));

    #{$that}__btn {
      svg {
        transform: rotate(180deg);
      }
    }

    #{$that}__container {
      visibility: visible;
    }

    #{$that}__nav {
      opacity: 1;
    }

    #{$that}__img {
      opacity: 1;
    }
  }

  &.dark {
    box-shadow: 8px 8px 12px #161618, -8px -8px 12px #2a2c30;
  }

  &__btn {
    margin-left: auto;
    display: block;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    border: 1px solid var(--accent);
    --accent: var(--bd-light);
    background-color: transparent;
    transition: border-color 0.15s ease-in-out, opacity 0.3s ease-in-out;
    outline-color: var(--blue);

    svg {
      position: absolute;
      inset: 4px;
      margin: auto;
      fill: var(--accent);
      transition: fill 0.15s ease-in-out;
    }

    &:hover,
    &.focus-visible {
      --accent: var(--blue);
    }
  }

  &__container {
    width: max-content;
    margin: 20px auto;
    visibility: hidden;
    white-space: nowrap;
  }

  &__nav {
    padding: 4px 0;
    opacity: 0;
    transition: opacity 0.3s ease-in-out 0.2s;

    .nav__list {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__img {
    width: 100%;
    max-width: 64px;
    margin: 0 auto;
    display: block;
  }
}

.check {
  padding: 2px 4px;
  display: inline-block;
  flex-shrink: 0;
  line-height: 1;
  cursor: pointer;
  --unchecked: rgb(182, 182, 182);
  --checked: var(--blue);

  &__content {
    min-height: 16px;
    position: relative;
    border-radius: 2px;
    line-height: 1;
    display: inline-block;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      height: 16px;
      transform: translateY(-50%);
    }

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }

    &--radio {
      padding-left: 32px;

      &::before {
        width: 32px;
        border-radius: 50px;
        background-color: var(--unchecked);
      }

      &::after {
        height: 12px;
        width: 12px;
        left: 2px;
        border-radius: 50%;
        background-color: #fff;
      }
    }

    &--checkbox {
      padding-left: 16px;

      &::before {
        width: 16px;
        border-radius: 4px;
        background-color: #fff;
        border: 1px solid var(--unchecked);
        margin-right: 40px;
      }
    }

    &:empty {
      --padding-left: 28px;
    }
  }

  &__text {
    display: inline-block;
    margin-left: 4px;
  }

  &__input:checked ~ &__content {
    &--radio {
      &::before {
        background-color: var(--checked);
      }

      &::after {
        left: 18px;
      }
    }

    &--checkbox {
      &::before {
        background-color: var(--checked);
        border-color: var(--checked);
      }

      &::after {
        width: 12px;
        height: 8px;
        border: 2px solid;
        border-color: transparent transparent #fff #fff;
        transform: translateY(-80%) rotate(-45deg);
        left: 2px;
      }
    }
  }

  &__input.focus-visible ~ &__content {
    outline: 2px solid var(--checked);
    outline-offset: 2px;
  }
}

.login {
  &__form {
    display: inline-block;
    width: 100%;
    max-width: 300px;
    margin: 64px auto 32px;
    padding: 32px 32px 16px;
    border-radius: 16px;
    box-shadow: 12px 12px 24px #ceced2, -12px -12px 24px #ffffff;
  }
}
