.group-wrap {
  padding: 16px 8px;
  position: relative;
  border-radius: 4px;
  border: 1px solid #d9d9d9;

  &__title {
    padding: 0 4px;
    position: absolute;
    top: 0;
    right: 8px;
    font-size: 1.6rem;
    line-height: 1;
    transform: translateY(-50%);
    background-color: var(--bg);
  }
}
